/* src/pages/order/OrderListPage.css */
.olp-card {
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 14px;
  padding: 16px;
  background: rgba(10, 10, 12, 0.85);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.35);
}

.olp-left {
  border-right: 1px dashed rgba(255, 255, 255, 0.12);
  padding-right: 12px;
}

@media (max-width: 767px) {
  .olp-left {
    border-right: none;
    padding-right: 0;
    border-bottom: 1px dashed rgba(255, 255, 255, 0.12);
    padding-bottom: 12px;
    margin-bottom: 6px;
  }
}

.olp-order-number {
  font-size: 18px;
  font-weight: 800;
  color: #fff;
  letter-spacing: 0.5px;
}

.olp-date {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.65);
  margin-top: 2px;
}

.olp-badges {
  margin-top: 10px;
}

.olp-line {
  color: rgba(255, 255, 255, 0.92);
  font-size: 14px;
  margin-bottom: 6px;
}

.olp-label {
  color: rgba(255, 255, 255, 0.65);
}

.olp-value {
  color: rgba(255, 255, 255, 0.95);
}

.olp-attendant {
  display: flex;
  align-items: center;
  gap: 8px;
}

.olp-attendant-box {
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.olp-avatar {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid rgba(255, 255, 255, 0.18);
  background: rgba(255, 255, 255, 0.06);
}

.olp-attendant-link {
  background: transparent;
  border: none;
  padding: 0;
  margin: 0;
  color: #7dd3fc;
  font-weight: 700;
  cursor: pointer;
  text-decoration: underline;
  text-underline-offset: 3px;
}

.olp-attendant-link:disabled {
  color: rgba(255, 255, 255, 0.4);
  cursor: not-allowed;
  text-decoration: none;
}

.olp-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
}

.olp-meta-item {
  border: 1px solid rgba(255, 255, 255, 0.10);
  border-radius: 12px;
  padding: 8px 10px;
  background: rgba(255, 255, 255, 0.04);
  min-width: 160px;
}

.olp-meta-k {
  display: block;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.55);
}

.olp-meta-v {
  display: block;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.92);
  margin-top: 2px;
  font-weight: 700;
}

.olp-price {
  font-size: 18px;
  font-weight: 900;
  color: #fff;
}

.olp-btn {
  white-space: nowrap;
}
