.neon-modal .modal-content {
  background: #0a0a0c;
  border: 1px solid #00ffff33;
  border-radius: 18px;
  color: #fff;
  box-shadow: 0 0 30px #00ffff22;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  max-height: 90vh;
}

.wizard-body {
  text-align: center;
  padding: 25px;
  overflow-y: auto;
  flex: 1;
  scroll-behavior: smooth;
}

/* Indicadores */
.wizard-steps {
  display: flex;
  justify-content: center;
  gap: 6px;
  margin-bottom: 15px;
}

.step-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #123;
  transition: 0.3s;
}

.step-dot.active {
  background: #00ffff;
  box-shadow: 0 0 8px #00ffff;
}

/* Contedo com fade */
.wizard-step {
  animation: fadeIn 0.5s ease;
}

/* Grade de cards com rolagem adaptvel */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 12px;
  justify-content: center;
  max-height: 55vh;
  overflow-y: auto;
  padding-right: 5px;
}

/* Card visual moderno */
.card-service,
.card-emp {
  background: #111;
  border: 1px solid #00ffff55;
  border-radius: 12px;
  padding: 12px;
  cursor: pointer;
  transition: 0.3s;
  text-align: center;
}

.card-service:hover,
.card-emp:hover {
  background: #00ffff22;
}

.card-service.active,
.card-emp.active {
  background: #00ffff;
  color: #000;
  box-shadow: 0 0 15px #00ffffaa;
}

.card-emp img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 8px;
}

/* Horrios */
.grid-times {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  max-height: 55vh;
  overflow-y: auto;
}

.time-btn {
  background: #111;
  color: #00ffff;
  border: 1px solid #00ffff55;
  border-radius: 10px;
  padding: 8px 14px;
  cursor: pointer;
  transition: 0.3s;
}

.time-btn:hover {
  background: #00ffff22;
}

.time-btn.active {
  background: #00ffff;
  color: #000;
  box-shadow: 0 0 10px #00ffff;
}

/* Texto e confirmao */
.empty-text {
  color: #888;
  padding: 15px;
}

.confirm-box {
  background: #0f1116;
  border-radius: 10px;
  padding: 15px;
  border: 1px solid #00ffff33;
  max-height: 55vh;
  overflow-y: auto;
}

.confirm-box ul {
  list-style: none;
  padding: 0;
  margin: 10px 0;
  text-align: left;
}

.confirm-box li {
  margin-bottom: 4px;
}

/* Rodap fixo */
.wizard-footer {
  border-top: 1px solid #111;
  background: #0b0d11;
  padding: 12px 25px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  position: sticky;
  bottom: 0;
  z-index: 2;
}

/* Suavidade geral */
.fade-in {
  animation: fadeIn 0.4s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Barra de rolagem customizada */
.grid::-webkit-scrollbar,
.confirm-box::-webkit-scrollbar,
.wizard-body::-webkit-scrollbar {
  width: 6px;
}
.grid::-webkit-scrollbar-thumb,
.confirm-box::-webkit-scrollbar-thumb,
.wizard-body::-webkit-scrollbar-thumb {
  background: #00ffff33;
  border-radius: 3px;
}
/* ===========================
    MOBILE FULLSCREEN STYLE
=========================== */
@media (max-width: 768px) {
  .neon-modal .modal-dialog {
    margin: 0;
    max-width: 100%;
    height: 100%;
  }

  .neon-modal .modal-content {
    border-radius: 0;
    width: 100%;
    height: 100%;
    max-height: 100%;
    box-shadow: none;
    display: flex;
    flex-direction: column;
  }

  .wizard-body {
    padding: 20px 15px 100px;
    overflow-y: auto;
    flex: 1;
  }

  .wizard-step h4 {
    font-size: 1.1rem;
    margin-bottom: 15px;
  }

  .grid {
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    gap: 10px;
  }

  .card-service,
  .card-emp {
    width: 100%;
    max-width: 160px;
    margin: 0 auto;
  }

  .wizard-footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 12px 18px;
    border-top: 1px solid #111;
    background: #0b0d11;
    box-shadow: 0 -3px 10px rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: space-between;
    z-index: 999;
  }

  .wizard-footer button {
    flex: 1;
    margin: 0 4px;
    font-size: 0.95rem;
  }

  .confirm-box {
    max-height: 60vh;
    overflow-y: auto;
    padding: 10px;
  }
}
/* =========================================================
    INPUTS DE CPF E TELEFONE (CONFIRM BOX)
   ========================================================= */
.confirm-box .form-label {
  color: #00ffffcc;
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 5px;
  text-align: left;
}

.confirm-box .form-control {
  background: #111317;
  border: 1px solid #00ffff33;
  color: #e0e0e0;
  border-radius: 10px;
  padding: 12px 16px;
  font-size: 0.95rem;
  box-shadow: inset 0 0 8px rgba(0, 255, 255, 0.08);
  transition: all 0.3s ease;
  outline: none !important;
}

.confirm-box .form-control::placeholder {
  color: #888;
  opacity: 0.8;
}

.confirm-box .form-control:focus {
  background: #0f1116;
  border-color: #00ffffaa;
  color: #fff;
  box-shadow: 0 0 12px #00ffff55, inset 0 0 8px #00ffff33;
}

.confirm-box .form-control:hover {
  border-color: #00ffff77;
}

/* Ajuste espaamento entre os dois campos */
.confirm-box .form-group,
.confirm-box .mb-2,
.confirm-box .mb-3 {
  margin-bottom: 16px !important;
}

/* =========================================================
    MELHOR HIERARQUIA VISUAL NA CONFIRMAO
   ========================================================= */
.confirm-box p {
  color: #bbb;
  font-size: 0.95rem;
  margin: 5px 0;
}

.confirm-box ul li {
  border-bottom: 1px solid #00ffff22;
  padding: 6px 0;
  color: #ccc;
}

/* =========================================================
    RESPONSIVIDADE DOS INPUTS
   ========================================================= */
@media (max-width: 576px) {
  .confirm-box .form-control {
    font-size: 0.9rem;
    padding: 10px 14px;
  }
}
